.modal{position:fixed;z-index:100;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.5)}.modal .modalText{margin-left:40px}.modal .modalText .modalClose:after{content:"×";display:flex;justify-content:end;font-size:2rem;font-weight:bold;cursor:pointer}.modal .modalText .modalTitle{margin-top:10px;font-size:2.5rem}.modal .modalText .modalFont{margin-top:40px;font-size:2rem}.modal .modalContent{position:absolute;top:50%;left:50%;width:450px;height:300px;padding:30px;transform:translate(-50%, -50%);border-radius:30px;background:#fff}.modal .modalBtn{display:flex;justify-content:space-around}.modal .modalButton:hover{background-color:#5a5a5a}.modal .modalButton{display:flex;justify-content:center;width:150px;margin-top:60px;padding:15px;border:none;border-radius:20px;background-color:#000;color:#fff;font-size:1.8rem;cursor:pointer}